.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.dashboard {
  background: transparent;
}
.dashboard .rowInfo-card .info-card .top {
  overflow: hidden;
}
.dashboard .rowInfo-card .info-card .top .right-part {
  float: right;
  text-align: right;
}
.dashboard .rowInfo-card .info-card .top .right-part .title {
  margin: 5px 0;
}
.dashboard .rowInfo-card .info-card .top .right-part .num {
  margin-bottom: 0;
  font-size: 26px;
  line-height: 30px;
}
.dashboard .rowInfo-card .info-card .top i {
  margin-top: 10px;
  float: left;
}
.dashboard .rowInfo-card .info-card .tips {
  margin-top: 16px;
  color: #969696;
}
.dashboard .weather-card {
  background-image: -webkit-gradient(linear, right top, left bottom, from(#d6eef6), color-stop(#dff0fa), color-stop(#e7f3fc), color-stop(#eff6fe), to(#f6f9ff));
  background-image: linear-gradient(to left bottom, #d6eef6, #dff0fa, #e7f3fc, #eff6fe, #f6f9ff);
  margin-bottom: 24px;
  height: 460px;
}
.dashboard .weather-card .weather-body {
  padding: 31px 29px;
  height: 347px;
  background: url(../../static/img/weather.7ca48b90.svg) center center / cover no-repeat;
}
.dashboard .weather-card .weather-date-location {
  padding: 0 0 38px;
  color: rgba(0, 0, 0, 0.65);
}
.dashboard .weather-card .weather-date-location h2 {
  color: rgba(0, 0, 0, 0.85) !important;
}
.dashboard .weather-card .weather-data {
  padding: 0 0 78px;
}
.dashboard .weather-card .weather-data h1 {
  color: rgba(0, 0, 0, 0.85) !important;
}
.dashboard .weather-card .weakly-weather {
  display: flex;
  background: var(--component-background);
  min-height: 100px;
  width: 100%;
}
.dashboard .weather-card .weakly-weather .weakly-weather-item {
  flex: 0 0 25%;
  border-right: 1px solid var(--border-color);
  text-align: center;
  min-height: 100px;
  padding: 24px;
  color: var(--heading-color);
}
.dashboard .weather-card .weakly-weather .weakly-weather-item:last-child {
  border-right: 0;
}
.dashboard .performance-card {
  margin-bottom: 24px;
  height: 460px;
}
.dashboard .performance-card .headerTitle {
  font-size: 14px;
  color: #71669e;
  margin-bottom: 27px;
}
.dashboard .global-trends {
  margin-bottom: 24px;
}
.dashboard .global-trends #chartContaniner {
  width: 100%;
  height: 350px;
}
.dashboard .text-1 {
  font-size: 13px;
}
.dashboard .text-3 {
  font-size: 40px;
}
.dashboard .mb-2 {
  margin-bottom: 8px;
}
.dashboard .mb-4 {
  margin-bottom: 24px;
}
.dashboard .warp-flex {
  display: flex;
}
.dashboard .warp-flex.jus-b {
  justify-content: space-between;
}
.dashboard .mb-0 {
  margin-bottom: 0;
}
.dashboard .mt-4 {
  margin-top: 24px;
}
